import { getMockListings, getMockListing } from './mockData';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || 'https://api.accommodation.com';
const USE_MOCK_DATA = process.env.NEXT_PUBLIC_USE_MOCK_DATA !== 'false';

export async function fetchListings(filters?: {
  city?: string;
  minPrice?: number;
  maxPrice?: number;
  roomType?: string;
  page?: number;
  limit?: number;
}): Promise<{
  listings: any[];
  meta: {
    page: number;
    limit: number;
    total: number;
    totalPages: number;
  };
}> {
  if (USE_MOCK_DATA) {
    const allListings = getMockListings({
      city: filters?.city,
      minPrice: filters?.minPrice,
      maxPrice: filters?.maxPrice,
      roomType: filters?.roomType,
    });

    const page = filters?.page || 1;
    const limit = filters?.limit || 12;
    const start = (page - 1) * limit;
    const end = start + limit;
    const listings = allListings.slice(start, end);

    return {
      listings,
      meta: {
        page,
        limit,
        total: allListings.length,
        totalPages: Math.ceil(allListings.length / limit),
      },
    };
  }

  const params = new URLSearchParams();
  if (filters?.city) params.append('city', filters.city);
  if (filters?.minPrice) params.append('minPrice', filters.minPrice.toString());
  if (filters?.maxPrice) params.append('maxPrice', filters.maxPrice.toString());
  if (filters?.roomType) params.append('roomType', filters.roomType);
  if (filters?.page) params.append('page', filters.page.toString());
  if (filters?.limit) params.append('limit', filters.limit.toString());

  try {
    const response = await fetch(`${API_BASE_URL}/listings?${params.toString()}`);
    if (!response.ok) {
      throw new Error('Failed to fetch listings');
    }
    return response.json();
  } catch (error) {
    const allListings = getMockListings({
      city: filters?.city,
      minPrice: filters?.minPrice,
      maxPrice: filters?.maxPrice,
      roomType: filters?.roomType,
    });

    const page = filters?.page || 1;
    const limit = filters?.limit || 12;
    const start = (page - 1) * limit;
    const end = start + limit;
    const listings = allListings.slice(start, end);

    return {
      listings,
      meta: {
        page,
        limit,
        total: allListings.length,
        totalPages: Math.ceil(allListings.length / limit),
      },
    };
  }
}

export async function fetchListing(id: string): Promise<any> {
  if (USE_MOCK_DATA) {
    const listing = getMockListing(id);
    if (!listing) {
      throw new Error('Listing not found');
    }
    return listing;
  }

  try {
    const response = await fetch(`${API_BASE_URL}/listings/${id}`);
    if (!response.ok) {
      throw new Error('Failed to fetch listing');
    }
    return response.json();
  } catch (error) {
    const listing = getMockListing(id);
    if (!listing) {
      throw new Error('Listing not found');
    }
    return listing;
  }
}

export async function login(email: string, password: string): Promise<{ user: any; token: string }> {
  if (USE_MOCK_DATA) {
    await new Promise((resolve) => setTimeout(resolve, 500));
    return {
      user: {
        id: '1',
        email,
        name: email.split('@')[0],
        userType: 'tenant',
      },
      token: 'mock-jwt-token-' + Date.now(),
    };
  }

  try {
    const response = await fetch(`${API_BASE_URL}/auth/login`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ email, password }),
    });
    if (!response.ok) {
      throw new Error('Login failed');
    }
    return response.json();
  } catch (error) {
    await new Promise((resolve) => setTimeout(resolve, 500));
    return {
      user: {
        id: '1',
        email,
        name: email.split('@')[0],
        userType: 'tenant',
      },
      token: 'mock-jwt-token-' + Date.now(),
    };
  }
}

export async function register(data: {
  email: string;
  password: string;
  name: string;
  userType: 'tenant' | 'owner';
}): Promise<{ user: any; token: string }> {
  if (USE_MOCK_DATA) {
    await new Promise((resolve) => setTimeout(resolve, 500));
    return {
      user: {
        id: Date.now().toString(),
        email: data.email,
        name: data.name,
        userType: data.userType,
      },
      token: 'mock-jwt-token-' + Date.now(),
    };
  }

  try {
    const response = await fetch(`${API_BASE_URL}/auth/register`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    });
    if (!response.ok) {
      throw new Error('Registration failed');
    }
    return response.json();
  } catch (error) {
    await new Promise((resolve) => setTimeout(resolve, 500));
    return {
      user: {
        id: Date.now().toString(),
        email: data.email,
        name: data.name,
        userType: data.userType,
      },
      token: 'mock-jwt-token-' + Date.now(),
    };
  }
}

export async function createListing(data: FormData): Promise<any> {
  const response = await fetch(`${API_BASE_URL}/listings`, {
    method: 'POST',
    body: data,
  });
  if (!response.ok) {
    throw new Error('Failed to create listing');
  }
  return response.json();
}

export async function getUserListings(): Promise<any[]> {
  if (USE_MOCK_DATA) {
    await new Promise((resolve) => setTimeout(resolve, 300));
    return [];
  }

  try {
    const token = localStorage.getItem('token');
    const response = await fetch(`${API_BASE_URL}/listings/my`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    if (!response.ok) {
      throw new Error('Failed to fetch user listings');
    }
    return response.json();
  } catch (error) {
    return [];
  }
}

