export function getMockListings(filters?: {
  city?: string;
  minPrice?: number;
  maxPrice?: number;
  roomType?: string;
}): Listing[] {
  let filtered = [...mockListings];

  if (filters?.city) {
    filtered = filtered.filter((listing) =>
      listing.city.toLowerCase().includes(filters.city!.toLowerCase())
    );
  }

  if (filters?.minPrice !== undefined) {
    filtered = filtered.filter((listing) => listing.price >= filters.minPrice!);
  }

  if (filters?.maxPrice !== undefined) {
    filtered = filtered.filter((listing) => listing.price <= filters.maxPrice!);
  }

  if (filters?.roomType) {
    filtered = filtered.filter((listing) => listing.roomType === filters.roomType);
  }

  return filtered;
}

export function getMockListing(id: string): Listing | null {
  return mockListings.find((listing) => listing.id === id) || null;
}

