'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useEffect, useState } from 'react';
import { isAuthenticated, getUser, logout } from '@/lib/auth';

export default function Navbar() {
  const pathname = usePathname();
  const [authenticated, setAuthenticated] = useState(false);
  const [user, setUserState] = useState<{ name: string; userType: string } | null>(null);

  useEffect(() => {
    setAuthenticated(isAuthenticated());
    setUserState(getUser());
  }, [pathname]);

  const handleLogout = () => {
    logout();
    setAuthenticated(false);
    setUserState(null);
    window.location.href = '/';
  };

  return (
    <nav className="sticky top-0 z-50 border-b border-gray-200/80 bg-white/80 backdrop-blur-lg">
      <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
        <div className="flex h-16 items-center justify-between">
          <Link href="/" className="flex items-center gap-3 group">
            <div className="flex h-10 w-10 items-center justify-center rounded-xl bg-gradient-to-br from-rose-600 to-pink-600 shadow-lg shadow-rose-600/25 transition-transform group-hover:scale-105">
              <svg className="h-6 w-6 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6" />
              </svg>
            </div>
            <span className="text-xl font-bold bg-gradient-to-r from-rose-600 to-pink-600 bg-clip-text text-transparent">Roomie</span>
          </Link>

          <div className="hidden items-center gap-8 md:flex">
            <Link
              href="/listings"
              className={`text-sm font-semibold transition-colors ${
                pathname === '/listings'
                  ? 'text-rose-600'
                  : 'text-gray-700 hover:text-rose-600'
              }`}
            >
              Browse Listings
            </Link>
            {authenticated ? (
              <>
                <Link
                  href="/dashboard"
                  className={`text-sm font-semibold transition-colors ${
                    pathname?.startsWith('/dashboard')
                      ? 'text-rose-600'
                      : 'text-gray-700 hover:text-rose-600'
                  }`}
                >
                  Dashboard
                </Link>
                <div className="flex items-center gap-4">
                  <span className="text-sm font-medium text-gray-700">{user?.name}</span>
                  <button
                    onClick={handleLogout}
                    className="rounded-lg bg-gray-100 px-4 py-2 text-sm font-semibold text-gray-700 transition-colors hover:bg-gray-200"
                  >
                    Logout
                  </button>
                </div>
              </>
            ) : (
              <div className="flex items-center gap-3">
                <Link
                  href="/login"
                  className="text-sm font-semibold text-gray-700 transition-colors hover:text-rose-600"
                >
                  Login
                </Link>
                <Link
                  href="/register"
                  className="rounded-xl bg-rose-600 px-5 py-2.5 text-sm font-semibold text-white shadow-lg shadow-rose-600/25 transition-all hover:bg-rose-700 hover:shadow-xl hover:shadow-rose-600/30"
                >
                  Sign Up
                </Link>
              </div>
            )}
          </div>

          <div className="md:hidden">
            <Link
              href={authenticated ? "/dashboard" : "/login"}
              className="text-sm font-semibold text-gray-700 hover:text-rose-600"
            >
              Menu
            </Link>
          </div>
        </div>
      </div>
    </nav>
  );
}
